<template lang="md">
  # Card

  ---

  通用卡片容器。

  ## 何时使用

  最基础的卡片容器，可承载文字、列表、图片、段落，常用于后台概览页面。

  ## API

  |参数      | 说明  | 类型       |默认值   |
  |---------|------|------------|--------|
  | title | 卡片标题 | String  | 无 |
  | width | 卡片宽度 | Number | 300 |

  <demo>
    <example title="无图片">
      <v-card title="Thumbnail label">
        <p>Cras justo odio, dapibus ac facilisis in, egestas eget quam. Donec id elit non mi porta gravida at eget metus. Nullam id dolor id nibh ultricies vehicula ut id elit.</p>
      </v-card>
    </example>
    <example title="有图片">
      <v-card title="Thumbnail label">
        <image :src="image1" slot="image">
        <p>Cras justo odio, dapibus ac facilisis in, egestas eget quam. Donec id elit non mi porta gravida at eget metus. Nullam id dolor id nibh ultricies vehicula ut id elit.</p>
        <v-button type="primary">确定</v-button>
        <v-button type="default" role="button">取消</v-button>
      </v-card>
    </example>
    <example title="可调整宽度">
      <v-card title="Thumbnail label" :width="250">
        <image :src="image1" slot="image">
        <p>Cras justo odio, dapibus ac facilisis in, egestas eget quam. Donec id elit non mi porta gravida at eget metus. Nullam id dolor id nibh ultricies vehicula ut id elit.</p>
        <v-button type="primary">确定</v-button>
        <v-button type="default" role="button">取消</v-button>
      </v-card>
    </example>
  </demo>
</template>
<script>
  import vCard from '../../components/card'
  import vButton from '../../components/button'

  export default {
    data () {
      return {
        image1: 'data:image/jpeg;base64,'
      }
    },
    components: {
      vCard,
      vButton
    }
  }
</script>



