module.exports = "<div class=markdown><h1 id=modal>Modal</h1> <hr> <p>模态对话框。</p> <h2 id=->何时使用</h2> <p>需要用户处理事务，又不希望跳转页面以致打断工作流程时，可以使用 Modal 在当前页面正中打开一个浮层，承载相应的操作。</p> <h2 id=api-modal>API - Modal</h2> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>visible</td> <td>对话框是否可见</td> <td>Boolean</td> <td>false</td> </tr> <tr> <td>title</td> <td>标题</td> <td>String</td> <td>无</td> </tr> <tr> <td>closable</td> <td>是否显示右上角的关闭按钮</td> <td>Boolean</td> <td>true</td> </tr> <tr> <td>okText</td> <td>确认按钮文字</td> <td>String</td> <td>确定</td> </tr> <tr> <td>cancelText</td> <td>取消按钮文字</td> <td>String</td> <td>取消</td> </tr> <tr> <td>confirmLoading</td> <td>确定按钮 loading</td> <td>Boolean</td> <td>false</td> </tr> <tr> <td>onOk</td> <td>点击确定回调</td> <td>function(e)</td> <td>无</td> </tr> <tr> <td>onCancel</td> <td>点击遮罩层或右上角叉或取消按钮的回调</td> <td>function(e)</td> <td>无</td> </tr> <tr> <td>footer</td> <td>底部内容，当不需要默认底部按钮时，可以设为false</td> <td>Boolean</td> <td>false</td> </tr> <tr> <td>maskClosable</td> <td>点击蒙层是否允许关闭</td> <td>Boolean</td> <td>false</td> </tr> <tr> <td>size</td> <td>大小，可选lg或者sm</td> <td>String</td> <td>无</td> </tr> </tbody> </table> <h2 id=api-confirmbox-modal-confirmbox->API - confirmBox (仅当modal=&quot;confirmBox&quot;生效)</h2> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>visible</td> <td>确认框是否可见</td> <td>Boolean</td> <td>false</td> </tr> <tr> <td>title</td> <td>标题</td> <td>String</td> <td>无</td> </tr> <tr> <td>content</td> <td>内容</td> <td>String</td> <td>无</td> </tr> <tr> <td>okText</td> <td>确认按钮文字</td> <td>String</td> <td>确定</td> </tr> <tr> <td>cancelText</td> <td>取消按钮文字</td> <td>String</td> <td>取消</td> </tr> <tr> <td>confirmLoading</td> <td>确定按钮 loading</td> <td>Boolean</td> <td>false</td> </tr> <tr> <td>onOk</td> <td>点击确定回调</td> <td>function(e)</td> <td>无</td> </tr> <tr> <td>onCancel</td> <td>点击遮罩层或右上角叉或取消按钮的回调</td> <td>function(e)</td> <td>无</td> </tr> <tr> <td>maskClosable</td> <td>点击蒙层是否允许关闭</td> <td>Boolean</td> <td>false</td> </tr> </tbody> </table> <div class=code-boxes> <div class=code-box> <h3>基本</h3> <div class=\"code-boxes-col-2-1 code-box-demo\"> <v-modal title=基本 :visible.sync=visible1> <p>这是一段modal内容</p> </v-modal> <v-button type=primary @click=\"visible1 = !visible1\">基本</v-button> </div> <div class=\"code-boxes-col-2-1 code-box-code\"><pre>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-modal</span> <span class=\"token attr-name\">title</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>基本<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">:visible.sync</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>visible1<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>p</span><span class=\"token punctuation\">></span></span>这是一段modal内容<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>p</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>v-modal</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-button</span> <span class=\"token attr-name\">type</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>primary<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">@click</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>visible1 <span class=\"token punctuation\">=</span> !visible1<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>基本<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>v-button</span><span class=\"token punctuation\">></span></span>\n</pre></div> </div> <div class=code-box> <h3>自定义文本</h3> <div class=\"code-boxes-col-2-1 code-box-demo\"> <v-modal title=自定义文本 :visible.sync=visible2 ok-text=Submit cancel-text=Return> <p>这是一段modal内容</p> </v-modal> <v-button type=primary @click=\"visible2 = !visible2\">自定义文本</v-button> </div> <div class=\"code-boxes-col-2-1 code-box-code\"><pre>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-modal</span> <span class=\"token attr-name\">title</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>自定义文本<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">:visible.sync</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>visible2<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">ok-text</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>Submit<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">cancel-text</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>Return<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>p</span><span class=\"token punctuation\">></span></span>这是一段modal内容<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>p</span><span class=\"to