module.exports = "<div class=markdown><h1 id=radio>Radio</h1> <hr> <p>单选框。</p> <h2 id=->何时使用</h2> <ul> <li>用于在多个备选项中选中单个状态。</li> <li>和 Select 的区别是，Radio 所有选项默认可见，方便用户在比较中选择，因此选项不宜过多。</li> </ul> <h2 id=api>API</h2> <h3 id=radio>Radio</h3> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> <th>可选值</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>checked</td> <td>指定当前是否选中</td> <td>Boolean</td> <td></td> <td>false</td> </tr> <tr> <td>defaultChecked</td> <td>初始是否选中</td> <td>Boolean</td> <td></td> <td>false</td> </tr> <tr> <td>value</td> <td>根据 value 进行比较，判断是否选中</td> <td>String</td> <td></td> <td>无</td> </tr> </tbody> </table> <h3 id=radiogroup>RadioGroup</h3> <p>单选框组合，用于包裹一组 <code>Radio</code>。</p> <table> <thead> <tr> <th>参数</th> <th>说明</th> <th>类型</th> <th>可选值</th> <th>默认值</th> </tr> </thead> <tbody> <tr> <td>onChange</td> <td>选项变化时的回调函数</td> <td>Function(e:Event)</td> <td>无</td> <td>无</td> </tr> <tr> <td>value</td> <td>用于设置当前选中的值</td> <td>String</td> <td>无</td> <td>无</td> </tr> <tr> <td>defaultValue</td> <td>默认选中的值</td> <td>String</td> <td>无</td> <td>无</td> </tr> </tbody> </table> <h2 id=->组件演示</h2> <div class=code-box> <h3>基本</h3> <div class=\"code-boxes-col-2-1 code-box-demo\"> <v-radio :default-checked=checked></v-radio> </div> <div class=\"code-boxes-col-2-1 code-box-code\"><pre>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-radio</span> <span class=\"token attr-name\">:default-checked</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>checked<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>v-radio</span><span class=\"token punctuation\">></span></span>\n</pre></div> </div> <div class=code-box> <h3>不可用</h3> <div class=\"code-boxes-col-2-1 code-box-demo\"> <v-radio :disabled=disabled></v-radio> <v-radio :checked.sync=checked :disabled=disabled></v-radio> <br/> <v-button type=default @click=_toggleChange>改变状态</v-button> </div> <div class=\"code-boxes-col-2-1 code-box-code\"><pre>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-radio</span> <span class=\"token attr-name\">:disabled</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>disabled<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>v-radio</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-radio</span> <span class=\"token attr-name\">:checked.sync</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>checked<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">:disabled</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>disabled<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>v-radio</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>br</span> <span class=\"token punctuation\">/></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>v-button</span> <span class=\"token attr-name\">type</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>default<span class=\"token punctuatio